import React, {useEffect, useRef, useState} from 'react';
import DesktopHeader from '../components/header';
import NavigationMenu from '../components/navigationBar';
import Footer from '../components/footer';
import {BrowserView,isWindows,MobileView} from 'react-device-detect';
import Video from '../components/videoTest';
import ImageTest from '../components/imageTest';

//realtime updating width and height value.
const useWidthSize = () =>
{
  const [size,setSize] = useState([window.innerHeight,window.innerWidth]);
  useEffect(() => {
    const handleResize = () =>{
      setSize([window.innerHeight, window.innerWidth]);
    }
    window.addEventListener("resize", handleResize);


  }, []);

  return size;
}

const Home = () =>
{
  const[height,width]= useWidthSize();
      //scroll system
  const listInnerRef = useRef();
  const onScroll = () => {
    if (listInnerRef.current) {
      const { scrollTop, scrollHeight, clientHeight } = listInnerRef.current;
    //  const temp = (scrollTop + clientHeight);
      if (scrollTop + clientHeight === scrollHeight) {
        console.log("reached bottom");
       // <Header></Header>
      }
      else if(((scrollTop + clientHeight+17)) === height) {
        console.log("reached top");
      }
      else  {
        
        console.log("windows height value: " + height);
      }
    }
  };
    return (
        //Only Website , Mobile view is only
        <>
     <BrowserView>
     <div onScroll={onScroll} ref={listInnerRef} style={{overflowY: "scroll", height: height, overflowX : "hidden"}}>
         <DesktopHeader/>
         <NavigationMenu/>
         <Video/>
         <ImageTest/>
         <Footer/>
     </div>
     </BrowserView>
      
     <MobileView>
        <div onScroll={onScroll} ref={listInnerRef} style={{overflowY: "scroll", height: height, overflowX : "hidden"}}>
           <DesktopHeader/>
         <NavigationMenu/>
         <Video/>
         <ImageTest/>
         <Footer/>
     </div>
     </MobileView>
     </> );
}
export default Home;