import videoSrc from '../media/intro-skyidea.mp4';
import styled from 'styled-components';
import { isWindows } from 'react-device-detect';



const VideoContainer = () => {

    return(
  <div style={{margin: "auto" ,width: "800px", minWidth: "200px", height: window.innerHeight }}>
        <video autoPlay loop muted style={{width: window.innerWidth, maxWidth: "800px"}}>
             <source src={videoSrc} type='video/mp4' /> 
             </video>
     </div>
                     )
}
export default VideoContainer;