import { Link } from 'react-router-dom';
import styled,{css} from 'styled-components';
import logo from '../media/SKY.png';
const Nametag = styled.div`
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 300;
    background: #EAEAEA;
    height: 200px;
    width: 100%;
    color: black;
    text-align: center;
`
const FooterLinks = styled.div`

    text-align: center;
    width: 280px;
    margin:auto;
    color: black;
`
const StyledLink = styled(Link)`
    text-decoration: none;
    font-size:15px;
    color: black;
`


const ContactLinks = styled.div`
    background: #232323;
    width:100%;
    height:80px;
`

const CopyRights = styled.div`
    color: #69B5F9;
    font-weight: 500;
    font-size: 13px;
    margin-top: 5px;
`
const PoweredBy = styled.div`
    background: #F29B00;
    width: 100%;
    height: 100px;
`


const DesktopHeader = () => {

    return( <Nametag>
        <img src={logo} style={{height: "40px" ,width: "auto", marginTop: "40px"}}/>
        <FooterLinks>
            <StyledLink to="/home"> Hanul Rheem </StyledLink> | 
            <StyledLink to="/projects"> Projects </StyledLink> |
            <StyledLink to="/meshArt"> 3D Artwork </StyledLink> |
            <StyledLink to="/lineArt"> 2D Artwork </StyledLink> 
            <StyledLink to="/contact"> Contact Me </StyledLink> 
            <CopyRights>© COPY RIGHT 2022 HANUL RHEEM</CopyRights>
        </FooterLinks>
        <ContactLinks>
            
        </ContactLinks>
        <PoweredBy>
            
        </PoweredBy>
    </Nametag>)
}
export default DesktopHeader