import styled,{css} from 'styled-components';
import logo from '../media/SKY.png';
import {Route, Link} from 'react-router-dom';
//pages
const MenuBar = styled.div`
    background: white;
    height: 50px;
    width: 100%;
    color: black;
    position: sticky;
    margin:auto;
    transition: 1s;
    top: 0;
`
const TextLeftElem = styled.div`
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 700;
    margin-top: 10px;
    padding: 5px;
    float:left;
    color: black;
`
const StyledLink = styled(Link)`
    text-decoration: none;
    color: black;
`
const ContactLink = styled(Link)`

    border-radius:#0054FC;
    text-decoration: none;
    color: white;
`
const TextRightElem = styled.div`
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 700;
    margin-top: 10px;
    margin-right: 30px;
    padding: 5px;
    background: skyblue;
    float:right;
    color: black;
    border-radius: 5px;
    background: #0054FC;
`

const NavigationMenu = () => {

    return( <MenuBar>
        <TextLeftElem>
            <StyledLink to="/home">
            <img src={logo} style={{height:"20px", width:"60px"}}/>
            </StyledLink>
        </TextLeftElem>
        <TextLeftElem>
            <StyledLink to="/projects">
            PROJECTS 
            </StyledLink>
            </TextLeftElem>
        <TextLeftElem>
            <StyledLink to="/lineArt">
            2D ART
            </StyledLink>
            </TextLeftElem>
        <TextLeftElem>
            <StyledLink to="/meshArt">
            3D ART
            </StyledLink>
            </TextLeftElem>
        <TextRightElem>
            <ContactLink to="/contact">CONTACT ME</ContactLink>
            </TextRightElem>
        </MenuBar>)
}
export default NavigationMenu;