import pic from '../media/mewt.PNG'
import styled from 'styled-components';
import { isWindows } from 'react-device-detect';


const PhotoContainer = () =>{

    return( 
        <div style={{width:"100%", background: "skyblue", height: window.innerHeight}}>
         <div style={{margin: "auto", height: window.innerHeight ,width: window.innerWidth }}>
        <img src={pic} alt= "logo" style={{height: window.innerHeight ,width: window.innerWidth, }}/>
         </div>
         
         </div>
         )
}
export default PhotoContainer;